import React from "react"
import './../index.css'

export default function Background() {
    return (
            <section style={{paddingTop: "2rem"}}>
                <p>
                    In 2020, news and opinion headlines in New York City’s leading newspapers depicted the seemingly alarming effect of the COVID-19 pandemic on city real estate. Rich neighborhoods "<a href='https://www.nytimes.com/interactive/2020/05/15/upshot/who-left-new-york-coronavirus.html' target='_blank'>emptied out</a>", the pandemic has "<a href='https://nypost.com/2020/07/10/covid-19-has-broken-nycs-housing-market/' target='_blank'>broken</a>" the housing market, data show a "<a href='https://nypost.com/2020/11/14/new-stats-reveal-massive-nyc-exodus-amid-coronavirus-crime/' target='_blank'>massive NYC exodus</a>."</p>
                <p>
                    But a year later, as the city remained mired in the pandemic, the housing market  "<a href='https://www.nytimes.com/2021/10/05/realestate/new-york-real-estate-sales.html' target='_blank'>surged</a>", "<a href='https://www.nytimes.com/2021/12/29/nyregion/chelsea-nyc-coronavirus.html' target='_blank'>bounce[d] back</a>", "made a <a href='https://www.nytimes.com/2021/12/31/realestate/in-2021-new-yorks-housing-market-made-a-stunning-comeback.html' target='_blank'>stunning comeback</a>", and "<a href='https://www.wsj.com/articles/manhattan-luxury-home-sales-skyrocketed-in-2021-11641301201' target='_blank'>skyrocketed</a>." These headlines  suggest that real estate has not just recovered, but is on the verge of a boom – that is, to follow the phases of the cycle detailed in the report "<a href='https://unhp.org/pdf/Foreclosure_Report_V111.pdf' target='_blank'>Corporate Windfalls or Social Housing Conversions?</a>" released by the nonprofit Community Service Society of New York in November 2020. As described, the market cycles from a "euphoria" stage high to a "crisis" stage low. But for investors, each phase whether high or low has the potential for profit or at least anticipated profit. For the tenants living in buildings owned by some investors or investment company, on the other hand, the cycle resembles more of a line of perpetual rent increases and lack of maintenance (Hornbach et al. 2020, 4).  
                </p>
                <p>
                    The pandemic certainly sparked extraordinary circumstances that left many unable to pay their rent, with debts mounting for both tenants and landlords. But at the root of these tensions is the reality that tenant fears of increasing rent and displacement, service cuts and deferred building maintenance, and changing ownership, stretch back decades (Hornbach et al. 2020, 3). In the past 25 years, tax reform, lending deregulation, cuts in urban funding, and the increasing growth and influence of the financial service industry paved the way for the financialization of housing (Gray 2021; Hornbach et al. 2020, 8; Klein et al. 2020, 20). Investors will scout out locations in which they foresee large-scale changes, such as rezoning and infrastructure development, in anticipation of real estate profits. This approach, known as property speculation, encourages predatory practices that push out long-term tenants (Klein et al. 2020, 19).
                </p>
                <p>
                    Speculation involves buying a building at a price higher than its worth – worth in the sense of generated income not reflecting the purchase price. In these cases, the likely plan of the buyer is to wait it out: They may take out another mortgage for a higher value and once approved, they keep the difference between the original amount they paid for the building, and the increased mortgage. This is known as "pulling out equity." Further, when the building's value goes up, the landlord can sell it off and make a profit from the gain between the amount they paid for the building and the new value (Gray 2021; Hornbach et al. 2020, 9).
                </p>
                <p>
                    To explain this, the "Corporate Windfalls or Social Housing Conversions?" report gives an example of a landlord that buys a building for $12 million with a mortgage of $10 million. When the property values in the area start to surge over the next few years, the landlord pushes long-term tenants out and takes in new tenants willing to pay higher rents. With a higher income now, the landlord can borrow more money from the bank by taking out a new mortgage of $12 million, thereby pulling in more money – termed as "pulling out equity." With a new mortgage and more money, the landlord can purchase more properties and build up their portfolio (Hornbach et al. 2020, 12).
                </p>
                <p>
                    Given this environment, both the headlines and the real estate cycle obscure the reality that for many renters in New York, much of the ongoing housing crises did not emerge during the pandemic; rather the pandemic exacerbated a situation some 25 years in the making. For my capstone project, with these considerations as the backdrop, I will explore a dataset of landlord incomes and expenses from the perspective of an analyst – whether that be a journalist, researcher or advocate – and use visualizations to navigate through the observations and try to pinpoint buildings exhibiting indicators of speculation. Beyond the scope of this project, the aim would ultimately be to locate buildings or areas of potential interest in a quest to find personal stories of tenants impacted by the aforementioned, in a city of persistent precarity.</p>
            </section>
    )
}